import React from 'react'
import PropTypes from 'prop-types';
import styled from 'styled-components';

import BaseHeader from "../../Structure/BaseHeader";
import SearchBar from "./SearchBar";

/* Styled Components */
/*     */
const SearchBarContainer = styled.div`
    flex: 1;
    height: 4rem;
    
    display: flex;
    justify-content: center;
    align-items: center;
`;


/* React Component */
const BoardHeader = (props) => {

    const {
        isHeaderOn,
        board,
        curUser,

        localLogoutRequest,
        outlookLogoutRequest,
        getBoardRequest
    } = props;

    const renderSearchBarComponent = () => {
        return (
            <SearchBarContainer>
                <SearchBar 
                    getBoardRequest={getBoardRequest} 
                    board={board}
                /> 
            </SearchBarContainer>
        )
    }

    return (
        <BaseHeader
            isHeaderOn={isHeaderOn}
            curUser={curUser}
            isBGWhite={false}
            doesNeedUserBadge={true}
            MiddleComponent={renderSearchBarComponent}

            localLogoutRequest={localLogoutRequest}
            outlookLogoutRequest={outlookLogoutRequest}
        />
    )
}

BoardHeader.propTypes = {
    // Properties
    isHeaderOn: PropTypes.bool.isRequired,              // Header  ,
    curUser: PropTypes.object.isRequired,               //  
    board: PropTypes.string.isRequired,                 //  topic  

    // Methods
    localLogoutRequest: PropTypes.func.isRequired,      //  
    outlookLogoutRequest: PropTypes.func.isRequired,    //   
    getBoardRequest: PropTypes.func.isRequired,         // board  , get    
}

export default BoardHeader;