

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Button } from "antd";

import PhotoUpload from './PhotoUpload'
import { ProfileBoxSubTitle } from "../ManageProfile";
import { colors } from "../../../../../../styles/variables";


/* Styled Components */
const WholeBox = styled.div`
    position: relative;
    flex: 2;
    height: 100%;

    display: flex;
    flex-flow: column nowrap;
`;

    const SubInfoText = styled.div`
        font-size: 0.875rem;
        color: ${colors.font_lightgray};
    `;

    const ProfileContainer = styled.div`
        /* margin: 2rem 0 0; */
        align-self: center;
    
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
    `;

        const PhotoContainer = styled.div`
            margin: 2rem 0 1.5rem;
            width: 18rem;
            height: 18rem;
            
            border-radius: 50%;
            border: 1px solid ${colors.gray_line};

            overflow: hidden;
        `;

        const UserEmail = styled.div`
            margin-bottom: 0.5rem;
            
            font-size: 0.875rem;
            color: ${colors.font_lightgray};

            cursor: default;
        `;

        const UserName = styled.div`
            font-size: 1rem;
            color: ${colors.font_darkgray};

            cursor: default;
        `;


    const ButtonContainer = styled.div`
        position: absolute;
        right: 0;
        bottom: 0.5rem;

        display: flex;
        flex-flow: row nowrap;
        align-items: flex-end;
    `;

        const StyledButton = styled(Button)`
            margin-left: 0.5rem;
            padding: 0rem 1rem;

            font-size: 0.75rem;
        `;



/* React Component */
const PhotoEditBox = (props) => {

    const {
        curUser,
        uploadedProfileImage,
        isDeleteProfileImage,

        uploadProfileImage,
        deleteUploadedProfileImage,
        initProfileImage,
        postProfileCreateRequest,
        postProfileUpdateRequest,
        postProfileDeleteRequest,
    } = props;

    const { username, email, pic_location } = curUser

    const isPhotoChanged = uploadedProfileImage || isDeleteProfileImage;

    console.log(uploadedProfileImage);

    /*   Backend POST     */
    const handlePost = () => {
        /*       update ,
                 create , 
              delete  */
        if ( uploadedProfileImage ) { 
            if (pic_location) { postProfileUpdateRequest(uploadedProfileImage) }
            else { postProfileCreateRequest(uploadedProfileImage) }
        }
        else { postProfileDeleteRequest(email) }
        window.location.reload();
    }

    return (
        <WholeBox>
            <ProfileBoxSubTitle>  </ProfileBoxSubTitle>
            <SubInfoText>      /   .</SubInfoText>

            <ProfileContainer>
                <PhotoContainer>
                    <PhotoUpload 
                        userPhoto={pic_location} 
                        uploadedProfileImage={uploadedProfileImage}
                        isDeleteProfileImage={isDeleteProfileImage}
    
                        uploadProfileImage={uploadProfileImage}
                        deleteUploadedProfileImage={deleteUploadedProfileImage}/>
                </PhotoContainer>
                <UserEmail> {email} </UserEmail>
                <UserName> {username} </UserName>
            </ProfileContainer>

        {
            isPhotoChanged &&
            <ButtonContainer>
                <StyledButton shape='round' danger onClick={initProfileImage} ></StyledButton>
                <StyledButton shape='round' onClick={handlePost} ></StyledButton>
            </ButtonContainer>
        }
        </WholeBox>
    )
}

PhotoEditBox.propTypes = {
    curUser: PropTypes.object.isRequired,                   //    
    uploadedProfileImage: PropTypes.oneOfType([             //   
        PropTypes.string,
        PropTypes.object
    ]),
    isDeleteProfileImage: PropTypes.bool.isRequired,        //   delete   

    uploadProfileImage: PropTypes.func.isRequired,          //   
    deleteUploadedProfileImage: PropTypes.func.isRequired,  //     
    initProfileImage: PropTypes.func.isRequired,            //    
    postProfileCreateRequest: PropTypes.func.isRequired,    //    POST  
    postProfileUpdateRequest: PropTypes.func.isRequired,    //    POST  
    postProfileDeleteRequest: PropTypes.func.isRequired,    //    POST  
}

export default PhotoEditBox;