

import React from 'react';
import moment from "moment";


export const addLineToString = (originStr) => {
    return originStr.split('\n').map( line => <span> {line} <br/> </span> )
}

/* dateStr => YYYY MM DD HH mm */
export const formatDateStrToFullDateTime = dateStr => moment(dateStr).format('YYYY MM DD HH mm')