


import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

import { SideBar, ContentArea } from './BodyComponents'
import { colors } from "../../styles/variables";


/* Styled Components */

/* Body Area  div 
   backgrond color ,     */
const BodyArea = styled.div`
    margin: 0;
    padding: 6.5rem 2.5rem 5rem;

    /*   50rem  ,    */
    min-height: ${props => {
        return props.windowHeight > 50 * 16 ?
        `${props.windowHeight}px` :
        '50rem';
    }};
    min-width: 1230px; /*      (1150px + 5rem) */

    background-color: ${colors.gray_bg};
`;

    /* Body   (SideBox, ContentBox)   Box
          , margin auto    
         . (  mobile   )
    
       Side Box Content Box  flex box */
    const WholeBox = styled.div`
        margin: 0 auto;
        padding-bottom: 5rem;
        width: 1150px;

        display: flex;
        flex-flow: row nowrap;
    `;


/* React Component */
const ManageBody = (props) => {

    const {
        curUser,
        kind,
        windowHeight,
        isLoading,

        uploadedProfileImage,
        isDeleteProfileImage,
        
        postList,
        
        postFeedbackDone,
        feedbackData,

        uploadProfileImage,
        deleteUploadedProfileImage,
        initProfileImage,
        postProfileUpdateRequest,
        postProfileCreateRequest,
        postProfileDeleteRequest,
        deletePost,
        updatePostCondition,
        storeFeedbackData,
        postFeedBack,
    } = props;

    return (
        <BodyArea windowHeight={windowHeight} >
            <WholeBox>
                <SideBar
                    curUser={curUser}
                    kind={kind}
                />
                <ContentArea 
                    isLoading={isLoading}
                    curUser={curUser}
                    kind={kind}
                    uploadedProfileImage={uploadedProfileImage}
                    isDeleteProfileImage={isDeleteProfileImage}
                    postList={postList}
                    postFeedbackDone={postFeedbackDone}
                    feedbackData={feedbackData}

                    uploadProfileImage={uploadProfileImage}
                    deleteUploadedProfileImage={deleteUploadedProfileImage}
                    initProfileImage={initProfileImage}
                    postProfileUpdateRequest={postProfileUpdateRequest}
                    postProfileCreateRequest={postProfileCreateRequest}
                    postProfileDeleteRequest={postProfileDeleteRequest}
                    deletePost={deletePost}
                    updatePostCondition={updatePostCondition}
                    storeFeedbackData={storeFeedbackData}
                    postFeedBack={postFeedBack}
                />
            </WholeBox>
        </BodyArea>
    )
}

ManageBody.propTypes = {
    curUser: PropTypes.object.isRequired,                   //    
    kind: PropTypes.string.isRequired,                      //   
    windowHeight: PropTypes.number.isRequired,              //     
    isLoading: PropTypes.bool.isRequired,                   // Manage   loading 

    uploadedProfileImage: PropTypes.oneOfType([             //   
        PropTypes.string,
        PropTypes.object
    ]),
    isDeleteProfileImage: PropTypes.bool.isRequired,        //   delete   
    postList: PropTypes.array,                              // Posts  
    postFeedbackDone: PropTypes.bool.isRequired,            //   POST   
    feedbackData: PropTypes.object.isRequired,              //  

    uploadProfileImage: PropTypes.func.isRequired,          //   
    deleteUploadedProfileImage: PropTypes.func.isRequired,  //     
    initProfileImage: PropTypes.func.isRequired,            //    
    postProfileUpdateRequest: PropTypes.func.isRequired,    //    POST  
    postProfileCreateRequest: PropTypes.func.isRequired,    //    POST  
    postProfileDeleteRequest: PropTypes.func.isRequired,    //    POST  
    deletePost: PropTypes.func.isRequired,                  // Post  
    updatePostCondition: PropTypes.func.isRequired,         //    
    storeFeedbackData: PropTypes.func.isRequired,           //    
    postFeedBack: PropTypes.func.isRequired,                //   POST  
}

export default ManageBody;