/*    Popover    
    : Header user , Carpool Modal 

   PopButton: Popover   ()
   contentList: Popover    Array 
   CustomContent:  Content   .
*/

import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Popover } from "antd";

import { colors } from "../../../styles/variables";
import './ant-popover.css'


/* Styled Components */
/* Content List  Item  */
const PopoverListItemStyle = css`
    padding: 0.375rem 1rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    white-space: nowrap;

    cursor: pointer;

    :hover {
        background-color: ${colors.gray_bg};
    }

    ${props => {
        switch (props.theme) {
            case 'danger':
                return css`
                    color: ${colors.font_red};
                `
            default: return
        }
    }}
`;

/* default type List Item */
const PopoverListItem = styled.div`
    ${PopoverListItemStyle}
`;

/* Link type List Item */
const PopoverListItemLink = styled(Link)`
    ${PopoverListItemStyle}

    color: ${colors.font_default};
    :hover {
        color: ${colors.font_default};
    }
`;

const PopoverListItemAtag = styled.a`
    ${PopoverListItemStyle}

    color: ${colors.font_default};
    :hover {
        color: ${colors.font_default};
    }
`;


    /* Content Item Icon  */
    const PopoverListItemIcon = styled.div`
        margin-right: 0.5rem;
    `;



/* React Component */
class UitdaPopover extends Component {

    state = { popoverVisible: false }

    /* Ant-Design Popover visible   */
    _handleVisibleChange = popoverVisible => {
        this.setState({ popoverVisible });
    };

    /*  content item    , popover   */
    _handleContentClick = (clickMethod) => {
        clickMethod();
        this.setState({
            popoverVisible: false,
        });
    }

    /* Content   list react component render  */
    _renderPopoverList = (contentList) => {
        return contentList.map( (contentData, idx) => {
            
            const { text, icon, clickMethod, type, url, theme } = contentData

            switch (type) {
                /* link type , Link  render */
                case 'link':
                    return (
                        <PopoverListItemLink to={url} key={idx} >
                            {
                                icon ?
                                <PopoverListItemIcon>{icon}</PopoverListItemIcon> :
                                ''
                            }
                            {text}
                        </PopoverListItemLink> 
                    )

                case 'a':
                    return (
                        <PopoverListItemAtag href={url} key={idx} >
                            {
                                icon ?
                                <PopoverListItemIcon>{icon}</PopoverListItemIcon> :
                                ''
                            }
                            {text}
                        </PopoverListItemAtag> 
                    )

                /* default type , clickMethod  div  render */
                default:
                    return (
                        <PopoverListItem onClick={() => this._handleContentClick(clickMethod)} key={idx} theme={theme} >
                            {
                                icon ?
                                <PopoverListItemIcon>{icon}</PopoverListItemIcon> :
                                ''
                            }
                            {text}
                        </PopoverListItem>
                    )
            }            

        });
    }


    render() {

        const { popoverVisible } = this.state;

        const {
            PopButton, CustomContent, contentList,
            placement,
        } = this.props;

        const content = CustomContent ? CustomContent : this._renderPopoverList(contentList);

        return (
            <Popover 
                content={content} 
                trigger='click' 
                title={false} 
                placement={placement} 
                visible={popoverVisible}
                onVisibleChange={this._handleVisibleChange}    
            >
                <PopButton />
                {''}
            </Popover>
        )
    }
}


UitdaPopover.propTypes = {
    PopButton: PropTypes.func.isRequired,       // Popover  trigger  

    contentList: PropTypes.array,               /* Popover   Array .  
                                                    elem text clickMethod    , 
                                                   icon     . */ 
    CustomContent: PropTypes.node,              // List  Custom Content .

    /* Popover Property */
    placement: PropTypes.string,                
}

UitdaPopover.defaultProps = {
    contentList: [],
    CustomContent: undefined,
    placement: 'bottomRight',
}

export default UitdaPopover;