

import React, { useState } from 'react';
import styled from 'styled-components';
import { Radio, Input } from 'antd';
import PropTypes from 'prop-types';

import { PRICE } from "../../../../../constants/edit_Input_Data_Keys";


/* Styled Components */
const WholeBox = styled.div`
    position: relative;
    margin-bottom: 2rem;

    display: flex;
    flex-flow: column nowrap;
    align-items: flex-end;
`;

    const RadioContainer = styled.div`
        margin-bottom: 1rem;
    `;

    const PriceInputTextArea = styled(Input)`
        padding-right: ${props => props.displayWon? '1.75rem' : '0.75rem' };
        width: 200px;

        /* TextArea  */
        resize: none;
        border-top: none;
        border-left: none;
        border-right: none;
        border-radius: 0;
        outline: 0;
        background-color: inherit;

        :focus {
            box-shadow: none;
        }

        font-size: 1.25rem;
        text-align: right;
    `;

    const WonMark = styled.span`
        position: absolute;
        top: 2.675rem;
        
        font-size: 1.25rem;
    `;



/* React Component */
const EditPrice = ({price, storeBoardData}) => {

    const defaultCondition = price === ' ' || price === ' ' ? price : ' ';

    const [ inputCondition, setInputCondition ] = useState(defaultCondition);
    const [ priceValue, setPriceValue ] = useState(price);

    const options = [
        { label: ' ', value: ' ' },
        { label: ' ', value: ' ' },
        { label: ' ', value: ' ' },
    ];
    
    const handleRadioChange = e => {
        setInputCondition(e.target.value);
        if ( e.target.value === ' ' ) { setPriceValue('') }   
        else { 
            setPriceValue(e.target.value) 
            storeBoardData(PRICE, e.target.value);
        } 
    }

    const isdisabled = !(inputCondition === ' ');
    const displayWon = !isdisabled && priceValue;

    return (
        <WholeBox>
            <RadioContainer>
                <Radio.Group options={options} onChange={handleRadioChange} value={inputCondition}/>
            </RadioContainer>
            <PriceInputTextArea 
                displayWon={displayWon}
                disabled={isdisabled}
                defaultValue={price}
                placeholder=" ."
                value={priceValue}
                onChange={(e) => {
                    setPriceValue(e.target.value);
                    storeBoardData(PRICE, e.target.value)
                }}
            />
            {
                displayWon &&
                <WonMark></WonMark>
            }
        </WholeBox>
    )
}


EditPrice.propTypes = {
    price: PropTypes.string,
    storeBoardData: PropTypes.func.isRequired
}

EditPrice.defaultProps = {
    price: ''
}

export default EditPrice;