//  : pages/manage.js


import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

/* Components */
import { ManageBody, ManageHeader, ManageFooter } from '../components/Manage'

/* Actions */
import {
    getMyProfileRequest,
    uploadProfileImage,
    deleteUploadedProfileImage,
    initProfileImage,
    getMyPostRequest,               // Posts GET request 
    deletePostRequest,              // Post Delete Post request   
    updatePostConditionRequest,
    postProfileUpdateRequest,
    postProfileCreateRequest,
    postProfileDeleteRequest,
    initFeedbackPage,
    storeFeedbackData,
    postFeedBackRequest,
    /*  */
    tempFunc,
} from '../store/actions/manage'
import { getStatusRequest, localLogoutRequest, outlookLogoutRequest } from '../store/actions/auth'

/* Constants */
import {
    MANAGE_PROFILE,
    MANAGE_POSTS_MARKET,
    MANAGE_POSTS_NETWORKING,
    MANAGE_COMMENTS,
    MANAGE_LIKEPOSTS,
    MANAGE_CONTACT,
} from '../constants/manage_category'
import {
    MARKET,
    NETWORKING,
} from '../constants/categories'


class ManageContainer extends Component {

    state = {}

    componentDidMount() {
        const {
            kind,
            getStatusRequest,
            getMyProfile,
            getMyPostRequest,
            initFeedbackPage,

            /*  */
            tempFunc,
        } = this.props

        let board;

        getStatusRequest();

        switch(kind) {

            /*   */
            case MANAGE_PROFILE:
                getMyProfile();
                break;

            /*   */
            case MANAGE_POSTS_MARKET:
                board = MARKET;
                // eslint-disable-next-line
            case MANAGE_POSTS_NETWORKING:
                board = board ? MARKET : NETWORKING;

                getMyPostRequest(board);
                break;

            /*    */
            case MANAGE_CONTACT:
                initFeedbackPage();
                break;

            /*    */
            case MANAGE_COMMENTS:
                // eslint-disable-next-line

            /*    */
            case MANAGE_LIKEPOSTS:
                // eslint-disable-next-line


            /* kind post  */
            default:
                tempFunc()
                break;
        }

        /* window size  ,   state  */
        window.addEventListener('resize', this._updateWindowSize)
        this._updateWindowSize()
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._updateWindowSize)
    }

    /* window  update */
    _updateWindowSize = () => {
        this.setState({
            ...this.state,
            windowHeight: window.innerHeight
        })
    }


    render() {

        const {
            isGetStatusDone,
            curUser,
            kind,

            isGetManageItemsDone,
            isManageItemsLoading,

            uploadedProfileImage,
            isDeleteProfileImage,

            postList,

            postFeedbackDone,
            feedbackData,

            localLogoutRequest,
            outlookLogoutRequest,
            deletePostRequest,
            updatePostConditionRequest,
            uploadProfileImage,
            deleteUploadedProfileImage,
            initProfileImage,
            postProfileUpdateRequest,
            postProfileCreateRequest,
            postProfileDeleteRequest,
            storeFeedbackData,
            postFeedBack,
        } = this.props;
    
        const { windowHeight } = this.state;

        const isLoaded = isGetStatusDone && isGetManageItemsDone;

        return(
            <div>
            {
                isLoaded ?
                
                    curUser ? 
                    <div style={{ minWidth:'1230px', position: 'relative' }} >
                        <ManageHeader
                            curUser={curUser}
                            localLogoutRequest={localLogoutRequest}
                            outlookLogoutRequest={outlookLogoutRequest}
                        />
                        <ManageBody 
                            curUser={curUser}
                            kind={kind}
                            windowHeight={windowHeight}
                            isLoading={isManageItemsLoading}
                            uploadedProfileImage={uploadedProfileImage}
                            isDeleteProfileImage={isDeleteProfileImage}
                            postList={postList}
                            postFeedbackDone={postFeedbackDone}
                            feedbackData={feedbackData}

                            deletePost={deletePostRequest}
                            updatePostCondition={updatePostConditionRequest}
                            uploadProfileImage={uploadProfileImage}
                            deleteUploadedProfileImage={deleteUploadedProfileImage}
                            initProfileImage={initProfileImage}
                            postProfileUpdateRequest={postProfileUpdateRequest}
                            postProfileCreateRequest={postProfileCreateRequest}
                            postProfileDeleteRequest={postProfileDeleteRequest}
                            storeFeedbackData={storeFeedbackData}
                            postFeedBack={postFeedBack}
                        /> 
                        <ManageFooter />
                    </div> :
                    <Redirect to='/' />:

                ' '
            }
            </div> 
        )
    }

}

ManageContainer.propTypes = {
    kind: PropTypes.string,
}

const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,                                   //  
        isGetStatusDone: state.auth.isGetStatusDone,                // get status   

        isGetManageItemsDone: state.manage.isGetManageItemsDone,    // Manage  GET   
        isManageItemsLoading: state.manage.isManageItemsLoading,    // Manage     

        uploadedProfileImage: state.manage.uploadedProfileImage,    //   
        isDeleteProfileImage: state.manage.isDeleteProfileImage,    //   delete   

        postList: state.manage.postList,                            // Posts GET    post list

        feedbackData: state.manage.feedbackData,                    //  
        postFeedbackDone: state.manage.postFeedbackDone,            //   POST   
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getStatusRequest: () => {dispatch(getStatusRequest())},                         //     
        localLogoutRequest: () => dispatch(localLogoutRequest()),                       //  
        outlookLogoutRequest: () => dispatch(outlookLogoutRequest()),                   //   

        getMyProfile: () => dispatch(getMyProfileRequest()),
        uploadProfileImage: (file) => dispatch(uploadProfileImage(file)),               //   
        deleteUploadedProfileImage: () => dispatch(deleteUploadedProfileImage()),       //     
        initProfileImage: () => dispatch(initProfileImage()),                           //    
        postProfileUpdateRequest: (file) => dispatch(postProfileUpdateRequest(file)),   //    POST 
        postProfileCreateRequest: (file) => dispatch(postProfileCreateRequest(file)),   //    POST 
        postProfileDeleteRequest: () => dispatch(postProfileDeleteRequest()),           //    POST 

        getMyPostRequest: (board) => {dispatch(getMyPostRequest(board))},               //    Post  GET request 
        deletePostRequest: (board, id) => {dispatch(deletePostRequest(board, id))},     // Post Delete POST request 
        updatePostConditionRequest: (board, id, condition) => {                         //    POST request 
            dispatch(updatePostConditionRequest(board, id, condition))
        },

        initFeedbackPage: () => dispatch(initFeedbackPage()),                           //    
        storeFeedbackData: (data_key, data_value) => {                                  //    
            dispatch( storeFeedbackData(data_key, data_value) )
        },
        postFeedBack: (feedbackData) => dispatch(postFeedBackRequest(feedbackData)),    //   POST  

        /*  */
        tempFunc: () => dispatch(tempFunc()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ManageContainer);