

import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import { Divider, Button } from 'antd';

import { colors } from "../../../../../styles/variables";
import { FeedbackSubmitImg } from "../../../../../styles/images";
import { ContentBoxStyle, BoxSubtitleStyle } from "../CommonComponents/CommonCSS";
import { ContentHeader } from "../CommonComponents";
import { BorderBoxStyle } from '../../../../../styles/CommonCSS';
import { UitdaTextArea } from "../../../../Structure/CommonComponents";
import { TITLE, DESCRIPTION } from "../../../../../constants/edit_Input_Data_Keys";


/* Styled Components */
const WholeBox = styled.div`
    ${ContentBoxStyle}
    height: 100%;
`;

    const EditFeedbackBox = styled.div`
        ${BorderBoxStyle}
        height: 100%;
        width: 100%;
        padding: 2rem;

        display: flex;
        flex-flow: column nowrap;
    `;

        const BoxInfoHeader = styled.div`
            ${BoxSubtitleStyle}
            flex: 0 0;
            margin-bottom: 2rem;

            color: ${colors.font_lightgray};
        `

        const TitleInputStyle = css`
            font-size: 1.25rem;
        `;

        const DescriptionInputContainer = styled.div`
            flex: 1 0;
        `;

            const DescriptionInputStyle = css`
                padding: 1rem;
                font-size: 1rem;
            `;

        const PostButton = styled(Button)`
            margin-top: 1rem;
            align-self: flex-end;
        `

    const AfterSubmitBox = styled(EditFeedbackBox)`
        padding: 3rem;
    `;

        const AfterSubmitTitle = styled.div`
            margin-bottom: 1.25rem;
            font-size: 2rem;
        `;

            const HighLightText= styled.span`
                color: ${colors.blue};
            `;

        const AfterSubmitDescription = styled.div`
            font-size: 1.25rem;
            font-weight: lighter;
        `;

        const AfterSubmitImg = styled.img`
            margin-top: 3rem;
            width: 15rem;
            align-self: center;
        `;


/* React Component */
const SendFeedback = (props) => {

    const {
        postFeedbackDone,
        feedbackData,
        postFeedBack,
        storeFeedbackData,
    } = props;

    const title = ' '

    return (
        <WholeBox>
            <ContentHeader title={title} />
            {
                postFeedbackDone ?
                // true ?
                
                /*   */
                <AfterSubmitBox>
                    <AfterSubmitTitle> 
                        <HighLightText> </HighLightText> <HighLightText></HighLightText>
                    </AfterSubmitTitle>
                    
                    <AfterSubmitDescription>       !</AfterSubmitDescription>
                    
                    <AfterSubmitImg src={FeedbackSubmitImg} alt='' />
                </AfterSubmitBox> :
                
                /*   */
                <EditFeedbackBox>
                    <BoxInfoHeader>     .</BoxInfoHeader>
                    
                    <UitdaTextArea 
                        size='100%'
                        customCSS={TitleInputStyle}
                        placeHolder=' .'
                        isUnderLine={false}
                        data_key={TITLE}
                        storeDataFunc={storeFeedbackData}
                    />

                    <Divider />

                    <DescriptionInputContainer>
                        <UitdaTextArea 
                            size='100%'
                            customCSS={DescriptionInputStyle}
                            placeHolder=' .'
                            isFullBorder={true}
                            isFullHeight={true}
                            data_key={DESCRIPTION}
                            storeDataFunc={storeFeedbackData}
                        />
                    </DescriptionInputContainer>

                    <PostButton shape='round' onClick={() => { postFeedBack(feedbackData) }} >  </PostButton>
                </EditFeedbackBox>
            }
        </WholeBox>
    )
}

SendFeedback.propTypes = {
    postFeedbackDone: PropTypes.bool.isRequired,            //   POST   
    feedbackData: PropTypes.object.isRequired,              //  
    storeFeedbackData: PropTypes.func.isRequired,           //    
    postFeedBack: PropTypes.func.isRequired,                //   POST  
}

export default SendFeedback;