

import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { colors } from "../../../../../styles/variables";


/* ,  ,    div  */
const HeaderBox = styled.div`
    margin-bottom: 2rem;

    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    justify-content: space-between;
`;

    /*   Title, SubInfo  div  */
    const TextBox = styled.div`
        display: flex;
        flex-flow: row nowrap;
        align-items: baseline;
    `;

        /*    */
        const Title = styled.div`
            font-size: 1.75rem;
        `;

        /*   */
        const SubInfo = styled.div`
            margin-left: 2rem;
            font-size: 1.125rem;
        `;

    /*    */
    const CreateButton = styled(Link)`
        padding: 0.375rem 1rem;

        color: ${colors.font_gray};
        font-size: 0.875rem;

        background-color: ${colors.white};
        border: 1px solid ${colors.gray_line};

        cursor: pointer;
        :hover {
            color: ${colors.font_darkgray};
            box-shadow: 0 0 3px rgba(0,0,0,.2);
        }
    `;


/* React Component */
const ManageContentHeader = ({title, subInfo, hasCreatedButton}) => {

    return (
        <HeaderBox>
            <TextBox>
                <Title>{title}</Title>
                <SubInfo>{subInfo}</SubInfo>
            </TextBox>
            {
                hasCreatedButton &&
                <CreateButton to='/manage/edit/newpost' ></CreateButton>
            }
        </HeaderBox>
    )
}

ManageContentHeader.propTypes = {
    title: PropTypes.string.isRequired,     // Title 
    subInfo: PropTypes.string,              // Sub Info 
    hasCreatedButton: PropTypes.bool,       //    
}

ManageContentHeader.defaultProps = {
    subInfo: '',
    hasCreatedButton: false,
}

export default ManageContentHeader;