//

//  : pages/Board

import React, { Component } from "react";
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import BoardBody from "../components/Board/BoardBody";
import BoardHeader from "../components/Board/BoardHeader";
import SideBar from "../components/Structure/SideBar";
import { 
    initiateBoard,
    getBoardRequest,
    getBoardRequestByScroll,
    boardHeaderOn,
    boardHeaderOff,
} from '../store/actions/board'
import { topicSelect } from "../store/actions/topic";
import { getStatusRequest, localLogoutRequest, outlookLogoutRequest } from "../store/actions/auth";

class BoardContainer extends Component {    

    state = {}

    componentDidMount() {
        const { 
            boardName,
            
            getStatusRequest,
            initiateBoard,
            getBoardRequest,
            headerOn,
            topicSelect,
        } = this.props;
        
        getStatusRequest();
        initiateBoard();

        headerOn();                                                 //  On
        topicSelect(boardName);                                     // app topic state boardName 
        
        getBoardRequest(boardName);

        window.addEventListener('scroll', this._handleScroll);      // Scroll   , onScroll 
        window.addEventListener('resize', this._updateWindowSize);  // window   ,   state 

        this._updateWindowSize();
    }

    componentWillUnmount () {
        window.removeEventListener("scroll", this._handleScroll);
        window.removeEventListener('resize', this._updateWindowSize);
    }

    _handleScroll = (e) => {
        const { 
            isHeaderOn, 
            isLast, isLoading, isFirstBoardGetSuccess,
            boardName, scroll, search,

            headerOff, headerOn,
            getBoardRequestByScroll,
        } = this.props;

        /* Scroll Heigth:    Height
           Scroll Top:   top   
           Client Height:   Height  */
        const scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);
        const scrollTop = Math.max(document.documentElement.scrollTop, document.body.scrollTop);
        const clientHeight = document.documentElement.clientHeight;

        // Header On/Off 
        if (isHeaderOn) { if(scrollTop > 64) { headerOff() } }
        else if (scrollTop <= 64) { headerOn() }

        /*   
               height 64px    ,
            GET  ,  GET loading  , isLast false
            GET  .  */
        if ( (scrollTop + clientHeight) > (scrollHeight - 64) ) {
            if( isFirstBoardGetSuccess && !isLast && !isLoading ) {
                getBoardRequestByScroll( boardName, scroll, search );
            }
        }
    }

    _updateWindowSize = () => {
        this.setState({
            ...this.state,
            windowHeight: window.innerHeight,
            windowWidth: window.innerWidth,
        })
    }


    render() {
        const {
            // properties
            curUser,
            isGetStatusDone,

            boardName,
            postlist,
            search,

            isHeaderOn,
            isFirstBoardGetSuccess,
            isLoading,

            // methods
            headerOn,
            localLogoutRequest,
            outlookLogoutRequest,
            getBoardRequest
        } = this.props;

        const { windowHeight, windowWidth } = this.state;

        return (
            isGetStatusDone ?

                curUser ? 
            
                <div>
                    <SideBar topic={boardName} />
                    
                    <BoardHeader 
                        isHeaderOn={isHeaderOn} 
                        curUser={curUser}
                        board={boardName}
                        
                        getBoardRequest={getBoardRequest} 
                        localLogoutRequest={localLogoutRequest}
                        outlookLogoutRequest={outlookLogoutRequest}
                    />
                    
                    {
                        isFirstBoardGetSuccess ?

                        <BoardBody 
                            boardName={boardName} 
                            postlist={postlist} 
                            search={search}
                            windowHeight={windowHeight}
                            windowWidth={windowWidth}

                            isLoading={isLoading}
                            isHeaderOn={isHeaderOn}
                            headerOn={headerOn}
                        /> :

                        <div className='PageError'>
                              :)
                        </div>
                    }
                </div> :

                <Redirect to='/' /> :

            'loading'
        )
    }
}

const mapStateToProps = (state) => {
    return {
        curUser: state.auth.user,                                   //     
        isGetStatusDone: state.auth.isGetStatusDone,                // get status   
        
        isFirstBoardGetSuccess: state.board.isFirstBoardGetSuccess, //   GET     = Render   
        postlist: state.board.postlist,                             // postlist 
        scroll: state.board.scroll,                                 //   (  )
        search: state.board.search,                                 //  
        isLoading: state.board.isLoading,                           // Scroll GET  
        isLast: state.board.isLast,                                 //    
        isHeaderOn: state.board.isHeaderOn,                         //  On 
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        headerOn: () => {dispatch(boardHeaderOn())},                    //    
        headerOff: () => {dispatch(boardHeaderOff())},                  //    

        getStatusRequest: () => dispatch(getStatusRequest()),           //          
        localLogoutRequest: () => dispatch(localLogoutRequest()),       //  
        outlookLogoutRequest: () => dispatch(outlookLogoutRequest()),   //   

        initiateBoard: () => {dispatch(initiateBoard())},               //    
        getBoardRequest: (boardName, scroll, search) => {               //  GET  
            dispatch(getBoardRequest(boardName, scroll, search))
        },
        getBoardRequestByScroll: (boardName, scroll, search) => {       //  Scroll GET  
            dispatch(getBoardRequestByScroll(boardName, scroll, search))
        },

        topicSelect: (topic) => {dispatch(topicSelect(topic))},         // App topic state topic  
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BoardContainer);