/* Manage, Edit Board, Chatting       
   Size   css , ,        ,
            MiddleComponent .  */

import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';

import UserBadgeBox from "./UserBadgeBox";
import { Logo } from '../CommonComponents';
import { colors, Screen_Size } from '../../../styles/variables'


/* Styled Compoents */
/*      */
const HeaderBox = styled.div`
    height: 4rem;
    background-color: ${ props => props.isBGWhite ? 
    colors.white : colors.blue };

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    box-shadow: 0 0 5px rgba(0,0,0,.05);
    /* border-bottom: 1px solid ${colors.gray_line}; */

    display: flex;
    flex-flow: row nowrap;

    opacity: 1;
    visibility: visible;

    transition: visibility 0.5s, 
                opacity 0.5s;

    ${props => !props.isHeaderOn && css`
        opacity: 0;
        visibility: hidden;
    `}
`; 

    /*     */
    const LogoContainer = styled.div`
        flex: 0 15rem;

        display: flex;
        justify-content: center;
        align-items: center;

        @media (max-width: ${Screen_Size.pad_portrait}) {
            flex-basis: 12rem;
        }
    `

    /*        div  
       MiddleComponent + UserBadgeBox */
    const ContentBox = styled.div`
        flex: 1;
        height: 100%;

        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
    `;


/* react component */
const BaseHeader = (props) => {
    
    const { 
        curUser,
        isHeaderOn,
        isBGWhite,
        MiddleComponent,
        doesNeedUserBadge,

        localLogoutRequest,
        outlookLogoutRequest,
    } = props;

    return (
        <HeaderBox isBGWhite={isBGWhite} isHeaderOn={isHeaderOn} >
            <LogoContainer> <Logo isWhite={!isBGWhite} /> </LogoContainer>

            <ContentBox>
                <MiddleComponent />
                {
                    doesNeedUserBadge ?
                    <UserBadgeBox 
                        curUser={curUser} 
                        localLogoutRequest={localLogoutRequest} 
                        outlookLogoutRequest={outlookLogoutRequest}    
                    /> :
                    ''
                }
            </ContentBox>
        </HeaderBox>
    )
}

BaseHeader.propTypes = {
    curUser: PropTypes.object,                          //   

    isHeaderOn: PropTypes.bool,                         // Header On  (Board   boolean )
    isBGWhite: PropTypes.bool,                          //  
    MiddleComponent: PropTypes.func.isRequired,         // Header   
    doesNeedUserBadge: PropTypes.bool,                  //      

    localLogoutRequest: PropTypes.func.isRequired,           //  
    outlookLogoutRequest: PropTypes.func.isRequired,    //   
}

BaseHeader.defaultProps = {
    isHeaderOn: true,
    isBGWhite: true,
    doesNeedUserBadge: true,
    curUser: {},
}

export default BaseHeader;